.App {
  text-align: center;
}

.App-logo {
  height: 90%;
  -webkit-box-shadow: -2px 2px 2px 1px rgba(57, 57, 57, 0.5);
          box-shadow: -2px 2px 2px 1px rgba(57, 57, 57, 0.5);
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
  border-radius: 5px;
}
.App-logo:hover {
  -webkit-box-shadow: -2px 2px 4px 3px rgba(57, 57, 57, 0.5);
          box-shadow: -2px 2px 4px 3px rgba(57, 57, 57, 0.5);
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02)
}

.App-header {
  background-color: #fff;
  height: 70vh;
  padding: 20px;
  color: white;
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
}

.App-title {
  font-size: 1.5rem;
}

.App-intro {
  font-size: large;
}

button {
  height: 3rem;
  font-size: 2rem;
  line-height: 2rem;
  padding: 0.5rem;
  border-radius: 5px;
  -webkit-box-shadow: -2px 2px 2px 1px rgba(57, 57, 57, 0.5);
          box-shadow: -2px 2px 2px 1px rgba(57, 57, 57, 0.5);
  -webkit-transition-duration: 0.3s;
       -o-transition-duration: 0.3s;
          transition-duration: 0.3s;
}
button:hover {
  -webkit-box-shadow: -2px 2px 4px 3px rgba(57, 57, 57, 0.5);
          box-shadow: -2px 2px 4px 3px rgba(57, 57, 57, 0.5);
  -webkit-transform: scale(1.02);
      -ms-transform: scale(1.02);
          transform: scale(1.02)
}


